.pos_box {
  display: grid;
  grid-template-columns: 1fr 1fr;

  grid-gap: 20px;

  .left {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
  }

  .right {
    border-radius: 10px;
    background-color: #ffffff;
    padding: 20px;
  }
}
.add_customer_btn {
  margin-bottom: -15px;
  // height: 6px;
}
.pos_table_row {
  th,
  td {
    &:first-child {
      width: 1px !important;
    }
    &:last-child {
      width: 1px !important;
    }
  }
}
.pos_table_row_total {
  td {
    font-size: 15px !important;
    font-weight: 700 !important;
    text-align: center;
    padding: 10px;
    color: #ffffff;
    background-color: rgb(255, 0, 64);
  }
}
.pos_product_list_search {
  grid-template-columns: 3fr 1fr;

  .pos_product_list_search_btn {
    margin-bottom: -15px;
  }
}
.pos_product_list {
    margin-top: 20px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;

  .product_card {
    border: 1px solid #dfe3e7;
    text-align: center;
    img {
      width: 80%;
      height: 80px;
      padding: 10px;
    }
    p {
      font-size: 13px;
      color: #505a6d;
      &:nth-child(3) {
        font-weight: 700;
        line-height: 0;
      }
    }
  }
}
