@import "../../style/__variables.scss";

.custom_form_style_month_year {
}
.react-datepicker-wrapper {
}

.react-datepicker__input-container {
}

.react-datepicker__input-container input {
  border-radius: 5px;
  width: 30%;
  padding: 5px;
  font-size: 1rem;
  font-weight: 400;
  color: #495057;
  background-color: rgb(255, 255, 255);
  border: 1px solid #688cb4;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  font-family: $font_ibm;

  &:focus {
    outline: none;
    // width: 10%;
  }

  &::placeholder {
    color: #dadada;
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    font-family: $font_ibm;
  }
}

.month_year_label {
  color: #7792b1;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  margin: 5px 0;
}
