@import "../../../style/__variables.scss";

.file_input_box {
  color: #7792b1;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  margin: 5px 0;
  // font-family: ;
  input {
    border-radius: 5px;
    // display: block;
    width: 100%;
    padding: 5px;
    font-size: 1rem;
    font-weight: 400;
    color: #495057;
    background-color: #fff;
    border: 1px solid #688cb4;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    font-family: $font_ibm;
    outline: none;
    box-shadow: none;
    &:focus {
      box-shadow: none;
    }
    &:active {
      border: none;
    }
  }
}
