$color-dark-blue: #1a233a;
// $color-dark-blue: #b3c2e9;
$color-light: #bdd1f8;
$color-text: #8a99b5;
$bgColor_primary: #fff;
$text_primary: #475f7b;
@import "../../style/_variables";
.wrapper {
  .main_header {
    height: 60px;
    z-index: 111;
    // position: relative;
    background-color: $bgColor_primary;
    position: fixed;
    width: 100%;
    // top: 0;
    .logo_box {
      border-right: 1px solid rgba(72, 94, 144, 0.16);
      border-bottom: 1px solid rgba(72, 94, 144, 0.16);
      padding-left: 30px;
      height: 60px;
      width: 20%;
      float: left;
      display: flex;
      transition: width 1s;
      // justify-content: center;
      align-items: center;
      button {
        background-color: transparent;
        border: none;
        color: $text_primary !important;
        font-size: 18px;
      }
      a {
        text-decoration: none;
        background-color: transparent;
        padding-left: 10px;
        .logos {
          color: $text_primary !important;
          font-size: 26px;
          font-weight: 700;
          span {
            color: red;
            font-size: 26px;
            font-weight: 700;
          }
        }
        &:first-child {
          position: relative;
          cursor: pointer;
          display: inline-block;
          overflow: hidden;
          z-index: 1;
          transition: 0.3s ease-out;
          color: $color-text;
          margin-right: 10px;
          svg {
            text-align: center;
            font-size: 20px;
          }
        }
      }
    }
    .navbar_main {
      min-height: 60px;
      border-bottom: 1px solid rgba(72, 94, 144, 0.16);
      padding: 0 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: $color-text;
      width: 80%;
      transition: width 1s;
      .left_header {
        svg {
          text-align: center;
          font-size: 20px;
        }
      }
      .right_header {
        svg {
          text-align: center;
          font-size: 26px;
          margin: 0 10px 0 0;
        }
      }
    }
  }

  .sidebar_main {

    
    .sidebar_nav {
      background-color: $bgColor_primary;
      border-right: 1px solid rgba(72, 94, 144, 0.16);
      padding: 20px 0 100px 20px;
      position: fixed;
      width: 20%;
      height: 100%;
      left: 0;
      transition: width 1s !important;
      overflow: auto;
      top: 60px;
      .navbar_section_info {
        color: #bec6d6;
        font-size: 12px;
        font-weight: 700;
        font-family: $font_ibm;
        text-transform: uppercase;
        line-height: 15px;
        margin: 10px 0;
        display: block;
        transition: display 0s;

      }
      li {
        list-style: none;
        margin-bottom: 10px;
        .navActiveLink {
          color: #5949d6;
          .nav_icon {
            background-color: #5949d6 !important;
            color: #fff;
          }
        }
        a {
          font-size: 1rem;
          font-weight: 600;
          text-decoration: none;
          color: $text_primary;
          display: flex;
          align-items: center;
          .nav_icon {
            padding: 6px 10px;
            background-color: #e8edf4;
            justify-self: center;
            margin-right: 10px;
            border-radius: 10px;
            color: $text_primary;
          }

          :hover {
            color: #5949d6;

            .nav_icon {
              background-color: #5949d6 !important;
              color: #fff;
              background-color: #5949d6 !important;
              z-index: 11111;
            }
          }
        }
      }
    }
  }
  .main_container {
    // display: grid;
    // grid-template-columns: 1fr 1fr;
  }

  .main_body {
    padding: 10px;
    padding-top: 70px;
    margin-left: 20%;
    background-color: #f4f5f9;
    min-height: 100vh;
    width: 80%;
    // transition: width 1s;
    // transition-timing-function: ease-out;
  
  
  }
}
