// desToggle
.desToggle_wrapper {
  .main_header {
    .logo_box {
      width: 5%;
      transition: width 1s;
      border-bottom: none;
      a {
        display: none;
      }
    }
    .navbar_main {
      width: 95%;
      transition: width 1s;
    }
  }

  .sidebar_main {
    // width: 5%;
    // transition: width 1s;

    .sidebar_nav {
      padding: 20px 0 0 20px;
      width: 5%;
      transition: width 1s;
      .navbar_section_info {
        display: none;
        transition: display 0s;
      }

      li {
        list-style: none;
        margin-bottom: 10px;
        a {
          .nav_text {
            display: none;
          }
        }
      }
    }
  }
  .main_body {
    margin-left: 5%;
    width: 95%;
  }
}

@media only screen and (max-width: 600px) {
  .wrapper {
    .main_header {
      width: 100%;
      margin-top: 30px;
      .logo_box {
        border-right: none;
        width: 20%;
        a {
          display: none;
        }
      }
      .navbar_main {
        justify-content: end;
        width: 80%;
        .left_header {
          display: none;
        }
      }
    }
    .sidebar_main {
      .sidebar_nav {
        background-color: $bgColor_primary;
        width: 260px;
        position: absolute;
        top: 90px;
        .navbar_section_info {
          display: block;
          transition: display 0s;
        }
      }

      .toggle_sidebar_nav {
        left: -260px;
      }
    }
    .main_body {
      margin-left: 0%;
      width: 100%;
      padding-top: 95px;
    }
  }
}

// mobile app logo start

.app_mobile_logo {
  display: none;
  z-index: 1111111;
  height: 30px;
  position: fixed;
  width: 100%;
  background-color: #fff;
  .app_logo {
    text-align: center;
    height: 30px;
    font-size: 1rem;
    font-weight: 600;
    text-decoration: none;
    color: $text_primary;
    .logos {
      color: $text_primary !important;
      font-size: 26px;
      font-weight: 700;
      span {
        color: red;
        font-size: 26px;
        font-weight: 700;
      }
    }
  }
}
@media only screen and (max-width: 600px) {
  .app_mobile_logo {
    display: block !important;
  }
}
// mobile app logo end
