@import "../../../style/__variables.scss";

.page_title_box {
  h1 {
    font-size: 24px;
    font-weight: 400;
    color: #475f7b;
    line-height: 29px;
    font-family: "Rubik", sans-serif;
  }
}

.breadcrumb_custom {
  color: #7792b1;
  text-decoration: none;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  &:hover {
    color: #7792b1;
  }
}

.breadcrumb_custom:after {
  color: green;
  content: "-";
  padding: 0 5px;
}

.breadcrumb_custom {
  &:last-child {
    color: #475f7b;
    &:after {
      content: "";
    }
  }
}
