.add_purchase_section {
  .add_purchase_btn {
    text-align: end;
  }
}
.supplier_form_style {
  grid-template-columns: 1fr 1fr;
  margin: 20px 0;
}

// confirmation modal
.modal_body_desing {
  padding-top: 70px;

  .img_responsive {
    max-width: 20%;
  }

  h1,
  p {
    padding: 20px;
  }
  p {
    text-align: center;
  }
  .fa {
    text-align: center;
    font-size: 40px;
    color: red;
  }
  .btn-primary {
    margin: 20px auto;
    display: block;
  }
  .close {
    cursor: pointer;
  }
}
