.edit_delete_button {
  border: none;
  background-color: transparent;
  color: #375de6;
  padding: 2px 4px;
  // margin: 5px 0;
  margin-right: 10px;
  border-radius: 5px;
  transition: background-color 0.5s;

  &:hover {
    background-color: #3c2bc1 !important;
    color: #fff;
  }
}
.redDelete {
  color: #ec2323;
  &:hover {
    background-color: #ec2323 !important;
  }
}
