@import "../../style/_variables";

.card_box_1 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
}
.div_2 {
  display: grid;
  grid-template-columns: 5fr 2.5fr 3fr;
  grid-gap: 10px;
  // width: 70%;
}
.div_3 {
  // width: 40%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  padding: 10px 0;
}
.shipping_animation{
  width: 100%;
  padding: 10px 0;
  border-radius: 15px;
}
.div_container{
  // width: 40%;
  display: grid;
  grid-template-columns: 4.5fr 5.5fr;
  grid-gap: 10px;

}

.des_box_header{
padding: 0px 10px;
  .des_box_title{
    font-size:18px;
    font-weight:500;
    color: #475f7b;
    font-family: $font_ibm;

  }
  .radialbar_1_title{
    margin: -14px 0 0 0!important;
  }
}
