.pagination_section {
  display: flex;
  justify-content: space-between;

  .showing_box {
    font-size: 14px;
    line-height: 21px;
    color: #7792b1;
  }

  .pagination_custom_box {
    ul {
      display: flex;
      li {
        list-style: none;

        a {
          text-decoration: none;
          padding: 5px 10px;
          color: #737373;
          border-radius: 2px;
          &:hover {
            background-color: #375de6;
            color: #fff;
            transition: background-color 0.7s;
          }
        }
        .paginationActive {
          background-color: #375de6;
          color: #fff;
        }
      }
    }
  }
}
